import { Toolbar, Box, Typography, Stack } from '@mui/material'
import React from 'react'
import { useSelector } from 'react-redux'

function Record() {
  const user = useSelector((state) => state.user.user)

  return (
    <Box sx={{ width: '100%' }} >
      <Stack direction={'column'} sx={{ width: '100%' }}>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='body2'> :</Typography>
          <Typography variant='body2'><b> {user.lifesCount} /</b></Typography>
        </Stack>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='caption'>:</Typography>
          <Typography variant='caption'><b> {user.record.prof}</b></Typography>
        </Stack>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='caption'> :</Typography>
          <Typography variant='caption'><b> {user.record.cashflow} </b></Typography>
        </Stack>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='caption'> :</Typography>
          <Typography variant='caption'><b> {user.record.bizCount}</b></Typography>
        </Stack>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='caption'> :</Typography>
          <Typography variant='caption'><b> {user.record.deposit} </b></Typography>
        </Stack>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='caption'>  :</Typography>
          <Typography variant='caption'><b> {user.record.rentCount}</b></Typography>
        </Stack>
        <Stack justifyContent="space-between" alignItems="flex-start" direction={'row'}>
          <Typography variant='caption'> :</Typography>
          <Typography variant='caption'><b> {user.record.houseSumm} </b></Typography>
        </Stack>
      </Stack>
    </Box>
  )
}

export default Record